.single-item {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: space-between;
  flex: 1;
  padding: 2rem 2rem 1.5rem;
  min-width: 12.5rem;
  min-height: 20rem;
}

.single-item .placeholder-item {
  box-shadow: none !important;
}

.single-item .listing-info-row {
  display: flex;
  justify-content: space-between;
}

.single-item .listing-info-row:not(.edit-mode) {
  flex-wrap: wrap;
}

.single-item .item-details .listing-info-row {
  margin-bottom: 1.5rem;
  height: 1.938rem;
}

.single-item .item-details .listing-info-row.edit-mode {
  margin-bottom: 0.125rem;
  height: auto;
}

.single-item .edit-button {
  border: 1px solid var(--teal) !important;
}

.single-item .title {
  font-size: 1.25rem;
  font-weight: 550;
  cursor: pointer;
}

.single-item .price {
  font-size: 1.25rem;
  max-width: 100%;
  word-break: break-all;
}

.single-item .title,
.single-item .price {
  padding-top: 0.15rem;
  max-width: 100%;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-align: left;
}

.single-item .price-input {
  max-width: 40%;
  margin-left: 1.2rem;
}

.single-item .image-container {
  position: relative;
  overflow: hidden;
  width: 100%;
  padding-bottom: 100%;
  border-radius: 5px;
  outline: 1px solid var(--lightGray);
  outline-offset: -1px;
}

.single-item .item-image {
  position: absolute;
  object-fit: cover;
  height: 100%;
  min-height: 100%;
  min-width: 100%;
}

.single-item .placeholder-image {
  width: 100% !important;
  height: auto !important;
  transform: none !important;
}

.single-item .image-container .image-input {
  position: absolute !important;
  width: calc(100% - 2px);
  padding: 1rem 0;
  top: calc(50% - 3.75rem);
  left: 1px;
}

.single-item .image-container .input-section {
  z-index: 100;
  width: 90%;
  left: 5%;
}

.single-item .image-container .input-and-label label {
  position: relative !important;
  top: 0;
}

.single-item .image-container .input-bg {
  position: absolute;
  top: 0;
  background-color: var(--white);
  opacity: 0.75;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.single-item .description:not(.edit-mode) {
  margin-top: 1.5rem;
}

.single-item .description.edit-mode {
  margin-top: 1.75rem;
  margin-bottom: -1.25rem;
}

.single-item .submission-info {
  margin-top: 1rem;
}

.single-item .author {
  font-weight: 550;
}

.single-item .save-delete-buttons {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-top: 0.5rem;
}

.single-item .save-button .button-content {
  gap: 0.33rem !important;
}

@media screen and (max-width: 766px) {
  .single-item .image-container {
    padding-bottom: 20rem;
  }
  .single-item .item-image {
    max-height: 20rem !important;
  }
}
